<template>
  <div class="list">
    <ul>
      <li class="list-li" v-for="(item,index) in items" v-show="toggleshow(index)" @click="toggleFinised(index)">
        <div class="radio"><i class="iconfont" :class="{'icon-selected':item.isFinished}"></i></div>
        <span :class="{deled:item.isFinished}">{{item.text}}</span>
        <i class="iconfont icon-close"></i>
      </li>
    </ul>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  data () {
    return {
    }
  },
  props: [
    'items','Completed','Active','All'
  ],
  methods: {
    toggleFinised(index){
      this.$emit('ischange',index)
    },
    toggleshow(index){
      if(this.All){
        return true
      }
      if(this.Active){
        return !this.items[index].isFinished
      }
      if(this.Completed){
        return this.items[index].isFinished
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul li{
  list-style: none;
  margin:0px;padding:0;
}
ul{ margin:0;padding:0; }
.list-li{
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  width:100%;
  height: 60px;
  border-bottom: 2px solid #eee;
}
.list-li i{
  padding: 15px;
  margin-right: 15px;
  font-size: 28px;
}
.list-li span{
  flex: 1;
  font-size: 28px;
  margin-left: 25px;
}
.icon-close{
  color: red;
  font-weight: bolder;
}
.deled{
  text-decoration:line-through;
  color: #bbb;
}
.radio{
  border: 1px solid #333333;
  border-radius: 100%;
  width:40px;
  height:40px;
  margin-left: 10px;
}
.icon-selected{
  position: relative;
  left: -20px;
  top: 3px;
  font-size: 25px;
  color:green;
  font-weight: bolder;
}
</style>



