<template>
  <div id="app">
    <div class="demo">
      <div id="top">
        <i class="iconfont icon-moreunfold"></i>
        <input class="text" placeholder="What needs to be done?" v-model="newItem" v-on:keyup.enter="adddNew"></input>
      </div>
      <list class="list" :items='this.items' :All="this.All" :Active="this.Active" :Completed="Completed" @ischange="ischange"></list>
      <div id="bottom">
        <div class="b-left">{{num}} items left</div>
        <div class="b-center">
          <span class="All"  :class="{'b-center-click': All }" @click="atclickAll">All</span>
          <span class="Active" :class="{'b-center-click': Active }" @click="atclickActive">Active</span>
          <span class="Completed" :class="{'b-center-click': Completed }" @click="atclickCompleted">Completed</span>
        </div>
        <div class="b-right" @click="ClearCompleted">Clear completed</div>
      </div>
    </div>
    <div class="bottombox"></div>
  </div>
</template>

<script>
import list from './components/list'

export default {
  name: 'app',
  components: {
    list
  },
  data(){
    return{
      All: true,
      Active: false,
      Completed: false,
      items:[{text:'1234',isFinished:false},{text:'1234',isFinished:false},{text:'1234',isFinished:false},{text:'1234',isFinished:true}],
      newItem:'',
      num:0
    }
  },
  methods: {
    adddNew(){
      this.items.push({
        text : this.newItem,
        isFinished : false
      })
      this.newItem = ''
    },
    ClearCompleted(){
      this.items.splice(0,this.items.length)
    },
    atclickAll(){
      this.All = !this.All;
      this.Active=false;
      this.Completed= false;
    },
    atclickActive(){
      this.Active = !this.Active;
      this.All=false;
      this.Completed= false;
    },
    atclickCompleted(){
      this.Completed = !this.Completed;
      this.All=false;
      this.Active= false;
    },
    ActiveNum(){
      this.num = 0;
      for(var i=0;i<this.items.length;i++){
        if(!this.items[i].isFinished){
          this.num++;
        }
      }
    },
    ischange(index){
      //this.items[index].isFinished = !this.items[index].isFinished;数组数据无法监听
      let obj = this.items[index];
      obj.isFinished = !obj.isFinished;
      this.$set(this.items, index, obj);
      this.ActiveNum()
    }
  },
  mounted: function () {
    console.log(444);
    this.ActiveNum();
  },
  computed:{
    
  }
}
</script>

<style>
#app {
  margin-top: 30px;
  width: 100%;
  height: 100%;
}
span{
   cursor:pointer
}
.list{
  height:68vh;
  overflow: auto;
  margin-bottom: 60px;
}
@media only screen and (max-width: 750px){
  .list{
    height:68vh;
    margin-bottom: 60px;
  }
}
.demo {
  background: #fff;
  z-index: 3;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 95vw;
  height: 100%;
  position: relative;
  left: 50%;
  margin-left: -47.5vw;
  min-height: 70vh;
  box-shadow: 0px 2px 5px #888888;
}
.bottombox {
  z-index: 2;
  color: #2c3e50;
  width: 94vw;
  height: 10px;
  position: relative;
  left: 50%;
  margin-left: -47vw;
  box-shadow: 0px 2px 5px #888888;
  backgroun: #fff;
}
.bottombox::after {
  display: block;
  content: "";
  z-index: 0;
  color: #2c3e50;
  width: 92vw;
  height: 10px;
  position: relative;
  left: 50%;
  top: 10px;
  margin-left: -46vw;
  box-shadow: 0px 2px 5px #888888;
  backgroun: #fff;
}
#top {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  top: 180px;
  border-bottom: 3px solid #eee;
  overflow: hidden;
}

.iconfont {
  cursor:pointer;
  font-size: 30px;
  color: #999;
  margin: 10px;
  padding-left: 10px;
}
.text {
  font-size: 30px;
  color: #999;
  margin-left: 20px;
  flex: 1;
  border: 0px;
  line-height: 60px;
  border:0;outline:none;
}
@media only screen and (max-width: 750px) {
  .text {
    width: 70vw;
    font-size: 20px;
  }
  .iconfont{
    font-size: 25px;
    line-height: 60px;
  }
}
#bottom {
  position: absolute;
  bottom: 0px;
  width: 97%;
  margin:15px;
  display: flex;
  justify-content: space-between;
}
.b-center span{
  margin-left:30px;
  padding:5px;
}
.b-center-click{
  border: 1px solid orangered;
  border-radius: 5px;
}
@media only screen and (max-width: 750px){
  #bottom{
    display:none
  }
}
</style>



